@use 'sass:color';

//* Colors

// bootstrap colors

$grey-100: hsl(210, 17%, 98%) !default;
$grey-200: hsl(210, 16%, 93%) !default;
$grey-300: hsl(210, 14%, 89%) !default;
$grey-400: hsl(210, 14%, 83%) !default;
$grey-500: hsl(210, 11%, 71%) !default;
$grey-600: hsl(208, 7%, 46%) !default;
$grey-700: hsl(210, 9%, 31%) !default;
$grey-800: hsl(210, 10%, 23%) !default;
$grey-900: hsl(210, 11%, 15%) !default;

$blue: hsl(211, 100%, 50%) !default;
$indigo: hsl(263, 90%, 51%) !default;
$purple: hsl(261, 51%, 51%) !default;
$pink: hsl(332, 79%, 58%) !default;
$red: hsl(354, 70%, 54%) !default;
$orange: hsl(27, 98%, 54%) !default;
$yellow: hsl(45, 100%, 51%) !default;
$green: hsl(134, 61%, 41%) !default;
$teal: hsl(162, 73%, 46%) !default;
$cyan: hsl(188, 78%, 41%) !default;

// my colors

$grey-100: hsl(210, 17%, 98%);
$grey-200: hsl(210, 16%, 93%);
$grey-300: hsl(210, 14%, 89%);
$grey-400: hsl(210, 14%, 83%);
$grey-500: hsl(210, 11%, 71%);
$grey-600: hsl(210, 7%, 46%);
$grey-700: hsl(210, 9%, 31%);
$grey-800: hsl(210, 10%, 23%);
$grey-900: hsl(210, 11%, 15%);

$blue: hsl(211, 100%, 50%);
$indigo: hsl(263, 90%, 51%);
$purple: hsl(261, 51%, 51%);
$pink: hsl(332, 79%, 58%);
$red: hsl(354, 70%, 54%);
$orange: hsl(27, 98%, 54%);
$yellow: hsl(45, 100%, 51%);
$green: hsl(134, 61%, 41%);
$teal: hsl(162, 73%, 46%);
$cyan: hsl(188, 78%, 41%);

$info: $cyan !default;
$success: $green !default;
$warning: $yellow !default;
$danger: $red !default;

$dark: $grey-700 !default;
$light: $grey-300 !default;

$pure-white: hsl(0, 0%, 100%);
$pure-black: hsl(0, 0%, 0%);

//* theme colors

// $primary: hsla(25, 100%, 65%, 1);
$primary: hsl(41, 100%, 55%);
$secondary: hsl(51, 14%, 10%);

$white: hsl(0, 0%, 100%);
$black: hsl(0, 0%, 4%);
$grey: hsl(0, 0%, 23%);
// $grey: hsl(47, 10%, 17%);

$text: $white;
$text-white: $white;
$text-black: $black;
$text-gray: hsl(0, 0%, 58%);
$text-disabled: color.change($text, $alpha: 0.4);

$box-shadow: 0px 4px 10px 0px hsla(50, 100%, 50%, 0.13);
$box-shadow-grey: 0px 2px 4px hsla(0, 0%, 0%, 0.25), inset 0px 0px 2px hsla(0, 0%, 100%, 0.2);

$filter-drop-shadow: drop-shadow(0px 4px 10px rgba(255, 213, 0, 0.13));

$button: hsl(0, 0%, 14%);
$button-bg: hsl(41, 100%, 55%);
// $button-border: hsl(0, 0%, 71%);

$button-hover: $button;
$button-bg-hover: $button-bg;
// $button-border-hover: $button-border;

$button-active: $button;
$button-bg-active: $button-bg;
// $button-border-active: $button-border;

$button-disabled: $text-disabled;
// $button-bg-disabled: color.grayscale($button-bg);
$button-bg-disabled: hsl(45, 5%, 85%);
$button-border-disabled: hsl(0, 0%, 71%);

$button-box-shadow: $box-shadow;

$checkbox-color: $secondary;
$checkbox: $checkbox-color;
$checkbox-active: $checkbox-color;
$checkbox-bg: hsla(0, 0%, 85%, 1);
$checkbox-bg-disabled: color.grayscale($checkbox-bg);

$bg-main: hsl(0, 0%, 8%);
$bg-black: $black;
// $bg-grey: hsla(47, 10%, 17%, 0.4);
$bg-grey: hsla(0, 0%, 23%, 0.4);
$bg-grey-dark: hsla(0, 0%, 14%, 0.4);

$border-grey: hsla(0, 0%, 23%, 0.4);

$scrollbar-track: hsla(47, 2%, 31%, 1);
$scrollbar-thumb: $primary;

//* necessary colors for JS
:root {
    --color-primary: #{$primary};
    --color-text: #{$text};
    --color-bg-main: #{$bg-main};
    --color-bg-section: #{$bg-grey};

    // --swiper-theme-color: #{$primary};
}

